@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
    --fnt-base: 'Jost', sans-serif;
    --clr-sharp-green: #99e55c;
    --clr-shine-green: #5aff6e;
    --clr-green: rgb(78, 84, 78);
    --clr-green-semi-transparen: rgba(78, 84, 78, 0.5);
    --clr-md-green: #646b64;
    --clr-dark: #595e59;
    --clr-white: #fff;
    --clr-whitesmoke: #f5f5f5;
    --clr-grey: #8e8e8e;
    --clr-lightgrey: #c7c7c7;
    --clr-white-transparent: rgba(255, 255, 255, 0.15);
    --spacing: 0.1rem;
    --gradient: linear-gradient(90deg, rgb(100, 245, 189) 0%, rgb(141, 255, 163) 100%);
    --transition: all 500ms ease;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

html {
    font-size: 10px;
}

body {
    font-size: 1.9rem;
    line-height: 1.6;
    font-family: var(--fnt-base);
    color: var(--clr-green);
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    opacity: inherit;
    color: unset;
}

h1, h2, h3, h4, h5, h6 {
    letter-spacing: var(--spacing);
    text-transform: capitalize;
}

img {
    width: 100%;
    display: block;
}

.img-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

button {
    cursor: pointer;
    outline: 0;
    border: none;
    background-color: transparent;
    font-family: inherit;
    font-size: 1.8rem;
}

input, textarea, select {
    outline: 0;
    border: none;
    resize: none;
}

/* custom classes */
.container {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 2rem;
}


/* flexbox and grid */
.flex {
    display: flex;
    align-items: center;
}

.flex-column {
    flex-direction: column;
}

.flex-center {
    justify-content: center;
}

.flex-between {
    justify-content: space-between;
}

.flex-start {
    justify-content: flex-start;
}

.flex-end {
    justify-content: flex-end;
}

.grid {
    display: grid;
}

/* height and width */
.h-100 {
    height: 100%;
}

.w-100 {
    width: 100%;
}

.text {
    line-height: 1.55;
    font-weight: 300;
    font-size: 19px;
    color: var(--clr-grey);
}

.text-lead {
    font-size: 2.5rem;
    line-height: 1.4;
    font-weight: 500;
    color: var(--clr-grey);
}

.text-center {
    text-align: center;
}

.text-start {
    text-align: left;
}

.text-end {
    text-align: right;
}

.text-uppercase {
    text-transform: uppercase;
}

.text-capitalize {
    text-transform: capitalize;
}

.text-grey {
    color: var(--clr-grey);
}

.text-white {
    color: var(--clr-white);
}

.text-black {
    color: var(--clr-green);
}

.fw-2 {
    font-weight: 200;
}

.fw-3 {
    font-weight: 300;
}

.fw-4 {
    font-weight: 400;
}

.fw-5 {
    font-weight: 500;
}

.fw-6 {
    font-weight: 600;
}

.fw-7 {
    font-weight: 700;
}

.fw-8 {
    font-weight: 800;
}

.fw-9 {
    font-weight: 900;
}

/* common font sizes */
.fs-14 {
    font-size: 14px;
}

.fs-15 {
    font-size: 15px;
}

.fs-16 {
    font-size: 16px;
}

.fs-17 {
    font-size: 17px;
}

.fs-18 {
    font-size: 18px;
}

.fs-19 {
    font-size: 19px;
}

.fs-20 {
    font-size: 20px;
}

.fs-21 {
    font-size: 21px;
}

.fs-22 {
    font-size: 22px;
}

.fs-23 {
    font-size: 23px;
}

.fs-24 {
    font-size: 24px;
}

.fs-25 {
    font-size: 25px;
}

.fs-26 {
    font-size: 26px;
}

/* letter spacing */
.ls-1 {
    letter-spacing: 1px;
}

.ls-2 {
    letter-spacing: 2px;
}

.bg-transparent {
    background-color: transparent;
}

.bg-white {
    background-color: var(--clr-white);
}

.bg-black {
    background-color: var(--clr-green);
}

.bg-semi-transparent {
    background-color: var(--clr-green-semi-transparen);
}

.bg-robin-blue {
    background-color: var(--clr-sharp-green);
}

.bg-dark {
    background-color: var(--clr-dark);
}

.bg-md-black {
    background-color: var(--clr-md-green);
}

.btn {
    min-height: 48px;
    min-width: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--clr-white);
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 0.95;
}

.btn-blue {
    background-color: var(--clr-sharp-green);
}

.mx-auto {
    margin-right: auto;
    margin-left: auto;
}

.resize-animation-stopper * {
    animation: none !important;
    transition: none !important;
}

.section-h {
    font-family: var(--fnt-base);
    font-size: 4.4rem;
    line-height: 1.4;
    font-weight: 600;
    margin-bottom: 8px;
}

.section-p {
    padding: 10rem 0;
}

.section-p-top {
    padding-top: 10rem;
    transition: var(--transition);
}

.section-t {
    font-size: 44px;
    color: var(--clr-white);
    padding: 20px 0;
    position: relative;
    line-height: 1.2;
}

.section-t p {
    margin-top: 26px;
}

.section-t::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    height: 5px;
    width: 60px;
    background: rgb(153, 229, 92);
    background: linear-gradient(45deg, rgb(102, 152, 62) 0%, rgb(109, 206, 32) 100%);
}

/* item */
.item-title {
    margin: 18px 0;
}

@media screen and (min-width: 768px) {
    .section-t {
        text-align: left;
    }

    .section-t::before {
        left: 0;
        transform: translateX(0);
    }
}

.translate-effect:hover {
    transform: translateY(-20px);
}